<template>
  <div class="activity-application-detail">
    <el-new-card v-loading="isLoading">
      <template slot="header">
        <div>
          {{ $route.meta.name }}
          <act-appl-sts-icon :status="info.status" point />
        </div>
      </template>

      <div>
        <h5 class="sub-title">活动信息</h5>
        <info-block>
          <info-item label="活动名称：">{{ info.name }}</info-item>
          <info-item label="所属部门：">{{ info.deptName }}</info-item>
          <info-item label="积分账户名称：">{{ info.account }}<acct-sts-icon :status="info.acctStatus" /></info-item>
          <info-item label="担当：">{{ info.creater }}</info-item>
          <info-item label="业务归属：">{{ info.busType }}</info-item>
          <info-item label="活动模式：">{{ info.mode }}</info-item>
          <info-item label="活动类型：">{{ info.type }}</info-item>
          <info-item label="平台：">{{ info.plat }}</info-item>
          <info-item label="开始时间：">{{ info.startTime | fmtDateTime }}</info-item>
          <info-item label="结束时间：">{{ info.endTime | fmtDateTime }}</info-item>
          <info-item label="累计活动天数：">{{ info.days }}</info-item>
          <info-item label="活动简介：">{{ info.intro }}</info-item>
        </info-block>
        <info-block>
          <info-item label="申请人：">{{ info.creater || '-' }}</info-item>
          <info-item label="申请时间：">{{ info.createTime | fmtDateTime }}</info-item>
          <info-item label="编辑人：">{{ info.editer || '-' }}</info-item>
          <info-item label="编辑时间：">{{ info.editTime || '-' }}</info-item>
          <info-item label="审批时间：">{{ info.updateTime || '-' }}</info-item>
        </info-block>

        <div class="btns">
          <el-button icon="el-icon-s-order" size="small" @click="popAttachments">查看附件</el-button>
          <option-btns
            :status="isAppling"
            :auth="optionsAuth"
            :aid="id"
            verify="serviceVerifyAppl"
            @onEdit="toEditAct"
            @onCancel="changeCancel"
            @onReject="changeNoPass"
            @onPass="changePass"
            @onClose="goBack"
          ></option-btns>
        </div>

        <el-divider></el-divider>

        <h5 class="sub-title">
          积分账户额度
          <span class="app-sts" :style="{ color: '#f60' }">
            <i :style="{ backgroundColor: '#f60' }"></i>
            提额申请中
          </span>
        </h5>
        <amount-acct :data="acctAmountInfo"></amount-acct>

        <el-divider></el-divider>

        <h5 class="sub-title">积分池配置</h5>
        <amount-config :data="configAmountInfo"></amount-config>

        <el-divider></el-divider>

        <h5 class="sub-title">业务规则</h5>
        <div class="rule-parts">
          <h5 class="sub-title">活动整体规则配置</h5>
          <el-table
            :data="info.wholeRules"
            stripe
            style="width: 100%">
            <el-table-column prop="rid" label="规则ID" align="center" width="120px"></el-table-column>
            <el-table-column prop="name" label="规则名称" min-width="130px"></el-table-column>
            <el-table-column prop="desc" label="规则说明" min-width="150px"></el-table-column>
            <el-table-column prop="startTime" label="开始时间" align="center" width="150px">{{ new Date() | fmtDateTime }}</el-table-column>
            <el-table-column prop="endTime" label="结束时间" align="center" width="150px">{{ new Date() | fmtDateTime }}</el-table-column>
            <el-table-column prop="status" label="状态" width="100px">未开始</el-table-column>
          </el-table>

          <h5 class="sub-title">用户行为规则配置 - 发行</h5>
          <template v-for="r in info.behaviorIssueRules">
            <h5 class="sub-title" :key="'issue-title' + r.id" style="margin-top: 40px;">
              <span>{{ r.name }}<i>未开始</i></span>
              <div>
                <div><strong>开始时间: </strong>活动同步</div>
                <div><strong>结束时间: </strong>活动同步</div>
              </div>
            </h5>
            <el-table
              :data="r.rules"
              stripe
              :key="'issue-table' + r.id"
              style="width: 100%">
              <el-table-column prop="rid" label="规则ID" align="center" width="120px"></el-table-column>
              <el-table-column prop="name" label="规则名称" min-width="130px"></el-table-column>
              <el-table-column prop="desc" label="规则说明" min-width="150px"></el-table-column>
              <el-table-column prop="startTime" label="开始时间" align="center" width="150px">{{ new Date() | fmtDateTime }}</el-table-column>
              <el-table-column prop="endTime" label="结束时间" align="center" width="150px">{{ new Date() | fmtDateTime }}</el-table-column>
            </el-table>
          </template>

          <h5 class="sub-title">用户行为规则配置 - 核销</h5>
          <template v-for="r in info.behaviorVerifiRules">
            <h5 class="sub-title" :key="'issue-title' + r.id" style="margin-top: 40px;">
              <span>{{ r.name }}<i>未开始</i></span>
              <div>
                <div><strong>开始时间: </strong>活动同步</div>
                <div><strong>结束时间: </strong>活动同步</div>
              </div>
            </h5>
            <el-table
              :data="r.rules"
              stripe
              :key="'verifi-table' + r.id"
              style="width: 100%">
              <el-table-column prop="rid" label="规则ID" align="center" width="120px"></el-table-column>
              <el-table-column prop="name" label="规则名称" min-width="130px"></el-table-column>
              <el-table-column prop="desc" label="规则说明" min-width="150px"></el-table-column>
              <el-table-column prop="startTime" label="开始时间" align="center" width="150px">{{ new Date() | fmtDateTime }}</el-table-column>
              <el-table-column prop="endTime" label="结束时间" align="center" width="150px">{{ new Date() | fmtDateTime }}</el-table-column>
            </el-table>
          </template>
        </div>
      </div>
    </el-new-card>

    <popup-atta :visible.sync="attaVisible" :fileList="info.attachments"></popup-atta>
  </div>
</template>

<script>
import ElNewCard from '@/components/ElNewCard'
import InfoBlock from '@/components/InfoBlock'
import InfoItem from '@/components/InfoItem'
import OptionBtns from '@/components/OptionBtns'
import AmountAcct from '@/components/amounts/AmountAcct'
import AmountConfig from '@/components/amounts/AmountConfig'
import AcctStsIcon from '@/components/status/AcctStsIcon'
import ActApplStsIcon from '@/components/status/ActApplStsIcon'
import PopupAtta from '@/views/popups/PopupAtta'
import { mapGetters } from 'vuex'

export default {
  name: 'account-application-detail',
  components: {
    ElNewCard,
    InfoBlock,
    InfoItem,
    OptionBtns,
    AmountAcct,
    AmountConfig,
    AcctStsIcon,
    ActApplStsIcon,
    PopupAtta
  },
  data () {
    return {
      id: '',
      info: {
        status: -1
      },
      acctAmountInfo: {},
      configAmountInfo: {},
      isLoading: false,
      attaVisible: false
    }
  },
  computed: {
    ...mapGetters({
      accountInfo: 'getAccountInfo'
    }),

    isAppling () {
      if (this.info.status === 0 || this.info.status === 3) return 'isAppling'
      return ''
    },

    optionsAuth () {
      // 判断是否可编辑或者管理员
      if (this.accountInfo.isAdmin) return 'isAdmin'
      if (this.info.deptId === this.accountInfo.orgId) return 'isEditor'
      return ''
    }
  },
  created () {
    this.id = this.$route.query.id
    if (this.id) {
      this.getDetail(this.id)
    } else {
      this.$router.replace('/404')
    }
  },
  methods: {
    getDetail (id) {
      this.isLoading = true
      this.$http.serviceGetActApplDetail({ id })
        .then(res => {
          this.info = res.info
          this.acctAmountInfo = res.acctAmountInfo
          this.configAmountInfo = res.configAmountInfo
        })
        .finally(() => {
          this.isLoading = false
        })
    },

    // 跳转到活动申请编辑
    toEditAct () {
      this.$router.push(`/activityMgmt/editActAppl?id=${this.id}`)
    },

    // 审批通过
    changePass () {
      this.changeApplicationStatus(0)
    },

    // 拒绝申请
    changeNoPass () {
      this.changeApplicationStatus(3)
    },

    // 取消申请
    changeCancel () {
      this.changeApplicationStatus(4)
    },

    // 更新申请状态
    changeApplicationStatus (status) {
      this.isLoading = true
      this.$http.serviceUpdateAcctApplSts({ status })
        .then(res => {
          this.info.status = status
        })
        .finally(() => {
          this.isLoading = false
        })
    },

    // 返回
    goBack () {
      this.$router.go(-1)
    },

    // 查看附件
    popAttachments () {
      this.attaVisible = true
    }
  }
}
</script>

